//      

export const initImageProtection = () => {
  //     
  document.addEventListener('contextmenu', (e) => {
    if (e.target.tagName === 'IMG') {
      e.preventDefault();
      return false;
    }
  });

  //   
  document.addEventListener('dragstart', (e) => {
    if (e.target.tagName === 'IMG') {
      e.preventDefault();
      return false;
    }
  });

  //       
  document.addEventListener('keydown', (e) => {
    // PrintScreen
    if (e.key === 'PrintScreen') {
      e.preventDefault();
      alert('    ');
      return false;
    }

    // Ctrl+S ()
    if ((e.ctrlKey || e.metaKey) && e.key === 's') {
      e.preventDefault();
      return false;
    }

    // Ctrl+Shift+S ( )
    if ((e.ctrlKey || e.metaKey) && e.shiftKey && e.key === 's') {
      e.preventDefault();
      return false;
    }

    // F12, Ctrl+Shift+I, Ctrl+Shift+J (DevTools) - 
    if (
      e.key === 'F12' ||
      ((e.ctrlKey || e.metaKey) && e.shiftKey && (e.key === 'I' || e.key === 'i')) ||
      ((e.ctrlKey || e.metaKey) && e.shiftKey && (e.key === 'J' || e.key === 'j'))
    ) {
      // e.preventDefault(); // ,    DevTools
      // return false;
    }
  });

  //   
  document.addEventListener('selectstart', (e) => {
    if (e.target.tagName === 'IMG') {
      e.preventDefault();
      return false;
    }
  });

  //  :    (  )
  let blurCount = 0;
  let lastBlur = 0;

  window.addEventListener('blur', () => {
    const now = Date.now();
    if (now - lastBlur < 2000) {
      blurCount++;
      if (blurCount > 3) {
        //       
        console.warn('   ');
      }
    } else {
      blurCount = 1;
    }
    lastBlur = now;
  });

  //      clipboard API
  document.addEventListener('copy', (e) => {
    const selection = window.getSelection();
    if (selection && selection.toString()) {
      //   
      return true;
    }
    //   
    e.preventDefault();
    return false;
  });

  //    "  "  
  document.addEventListener('touchstart', (e) => {
    if (e.target.tagName === 'IMG') {
      e.target.style.webkitTouchCallout = 'none';
    }
  });

  console.log('  ');
};

//      
export const addWatermark = (imageElement) => {
  if (!imageElement || imageElement.tagName !== 'IMG') return;

  const wrapper = document.createElement('div');
  wrapper.style.position = 'relative';
  wrapper.style.display = 'inline-block';
  wrapper.style.userSelect = 'none';

  const watermark = document.createElement('div');
  watermark.textContent = 'ELTOK.KZ';
  watermark.style.position = 'absolute';
  watermark.style.top = '50%';
  watermark.style.left = '50%';
  watermark.style.transform = 'translate(-50%, -50%) rotate(-45deg)';
  watermark.style.fontSize = '48px';
  watermark.style.color = 'rgba(255, 255, 255, 0.1)';
  watermark.style.fontWeight = 'bold';
  watermark.style.pointerEvents = 'none';
  watermark.style.userSelect = 'none';
  watermark.style.zIndex = '10';

  if (imageElement.parentNode) {
    imageElement.parentNode.insertBefore(wrapper, imageElement);
    wrapper.appendChild(imageElement);
    wrapper.appendChild(watermark);
  }
};

export default initImageProtection;
