import React, { useState, useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
// import DeliveryInfo from '../components/DeliveryInfo';
import '../styles/Product.css';
import '../styles/Checkout.css';
import { useLocation, useNavigate } from 'react-router-dom';
import { trackFormSubmit, trackPurchaseComplete } from '../utils/analytics';

const Checkout = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const product = location.state?.product;
  const [payment, setPayment] = useState('cashless');
  const [paymentMethod, setPaymentMethod] = useState('remote'); // remote, qr, transfer
  const [deliveryType, setDeliveryType] = useState('pickup'); // pickup, delivery
  const [selectedCity, setSelectedCity] = useState('');
  const [selectedDelivery, setSelectedDelivery] = useState(null);
  const [form, setForm] = useState({ firstName: '', phone: '', address: '', comment: '' });
  const [promo, setPromo] = useState('');
  const [siteSettings, setSiteSettings] = useState({ showPromoCode: false });

  useEffect(() => {
    const fetchSiteSettings = async () => {
      try {
        const response = await fetch('https://electro-1-vjdu.onrender.com/api/information');
        if (response.ok) {
          const data = await response.json();
          if (data.information) {
            setSiteSettings({
              showPromoCode: data.information.showPromoCode || false
            });
          }
        }
      } catch (error) {
        console.log('   :', error);
      }
    };
    
    fetchSiteSettings();
  }, []);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    //   
    if (!form.firstName || !form.phone) {
      alert(',    ');
      return;
    }
    
    if (deliveryType === 'delivery' && !form.address) {
      alert(',   ');
      return;
    }
    
    //     
    const orderDetails = {
      product: product?.name,
      price: total,
      deliveryType: deliveryType === 'pickup' ? '' : '',
      address: deliveryType === 'delivery' ? form.address : '.   216, ',
      payment: payment === 'cash' ? '' : ' ',
      paymentMethod: payment === 'cashless' ? (paymentMethod === 'remote' ? ' ' : paymentMethod === 'qr' ? 'QR-' : paymentMethod === 'transfer' ? '' : '') : '',
      customer: form.firstName,
      phone: form.phone,
      comment: form.comment,
      total: finalTotal
    };
    
    //        Telegram
    (async () => {
      let orderId = null;
      try {
        const resp = await fetch('https://electro-1-vjdu.onrender.com/api/orders', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            firstName: form.firstName,
            phone: form.phone,
            address: deliveryType === 'delivery' ? form.address : '',
            comment: form.comment,
            items: product ? [{ productId: product._id, productName: product.name, price: Number(total), quantity: 1 }] : [],
            deliveryType,
            payment,
            paymentMethod: payment === 'cashless' ? paymentMethod : null,
            total: Number(finalTotal)
          })
        });
        const data = await resp.json();
        if (!data.success) throw new Error('  ');
        orderId = data.orderId || Date.now().toString();
      } catch (err) {
        console.log('  :', err.message);
        orderId = Date.now().toString(); // fallback ID
      }
      try {
        await fetch('https://electro-1-vjdu.onrender.com/api/send-telegram', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            name: form.firstName,
            phone: form.phone,
            message: `  \n: ${product?.name || ''}\n: ${orderDetails.deliveryType}${deliveryType==='delivery' ? `, : ${form.address}` : ''}\n: ${orderDetails.payment}${payment==='cashless' ? `, : ${orderDetails.paymentMethod}` : ''}\n: ${Number(finalTotal).toFixed(0)} \n: ${form.comment || ''}`
          })
        });
      } catch (err) {
        console.log('   Telegram:', err.message);
      }
      
      //   
      if (product) {
        trackPurchaseComplete(
          orderId,
          product._id,
          product.name,
          finalTotal,
          'checkout_page'
        );
      }
      
      //   
      trackFormSubmit('checkout_form', product?._id);
      
      //    
      navigate('/thanks');
    })();
  };

  const total = product ? Number(product.price) : 0;
  const deliveryCost = deliveryType === 'delivery' ? 5000 : 0; //   5000 
  const finalTotal = total + deliveryCost;

  return (
    <div className="product-page">
      <Header />
      <main className="product-main" style={{ minHeight: 500 }}>
        <div className="checkout-container">
          {/*   -  */}
          <div className="checkout-products">
            <h1 className="product-title" style={{ marginBottom: 16 }}> </h1>

            {/* 1.   */}
            <div className="checkout-form-container" style={{marginBottom: 16}}>
              <div style={{fontWeight:700, marginBottom:12}}><span style={{color:'#FFB300'}}>1</span>  </div>
              <div className="form-group">
                <label></label>
                <input type="tel" name="phone" value={form.phone} onChange={handleChange} required placeholder="+7 (___) ___-__-__" />
              </div>
              <div className="form-group">
                <label></label>
                <input type="text" name="firstName" value={form.firstName} onChange={handleChange} required placeholder="  " />
              </div>
            </div>

            {/* 2.  */}
            <div className="checkout-form-container" style={{marginBottom: 16}}>
              <div style={{fontWeight:700, marginBottom:12}}><span style={{color:'#FFB300'}}>2</span> </div>
              <div className="delivery-methods">
                <label style={{display:'flex', alignItems:'center', marginBottom:8, cursor:'pointer'}}>
                  <input 
                    type="radio" 
                    name="delivery" 
                    value="pickup" 
                    checked={deliveryType === 'pickup'} 
                    onChange={() => setDeliveryType('pickup')}
                    style={{marginRight:6}}
                  />
                  
                </label>
                <label style={{display:'flex', alignItems:'center', cursor:'pointer'}}>
                  <input 
                    type="radio" 
                    name="delivery" 
                    value="delivery" 
                    checked={deliveryType === 'delivery'} 
                    onChange={() => setDeliveryType('delivery')}
                    style={{marginRight:6}}
                  />
                  
                </label>
              </div>
              
              {/*     */}
              {deliveryType === 'delivery' && (
                <div style={{marginTop:12, paddingLeft:20}}>
                  <div className="form-group">
                    <label> </label>
                    <input 
                      type="text" 
                      name="address" 
                      value={form.address} 
                      onChange={handleChange} 
                      placeholder="  " 
                      required={deliveryType === 'delivery'}
                    />
                  </div>
                </div>
              )}
              {deliveryType === 'pickup' && (
                <div style={{marginTop:12, paddingLeft:20}}>
                  <div style={{color:'#666', fontSize:14, padding:'8px 12px', background:'#f5f5f5', borderRadius:4}}>
                    : .   216, 
                  </div>
                </div>
              )}
            </div>

            {/* 3.  */}
            <div className="checkout-form-container" style={{marginBottom: 16}}>
              <div style={{fontWeight:700, marginBottom:12}}><span style={{color:'#FFB300'}}>3</span> </div>
              <div className="payment-methods">
                {/*   */}
                <div style={{display:'flex', flexDirection:'column', gap:8}}>
                  <label style={{display:'flex', alignItems:'center', cursor:'pointer'}}>
                    <input 
                      type="radio" 
                      name="payment" 
                      value="cashless" 
                      checked={payment==='cashless'} 
                      onChange={()=>setPayment('cashless')}
                      style={{marginRight:6}}
                    />
                     
                  </label>
                  {/* .      */}
                  {payment === 'cashless' && (
                    <div style={{marginLeft:20}}>
                      <div style={{marginBottom:8}}>
                        <label style={{display:'flex', alignItems:'center', cursor:'pointer'}}>
                          <input 
                            type="radio" 
                            name="paymentMethod" 
                            value="remote" 
                            checked={paymentMethod==='remote'} 
                            onChange={()=>setPaymentMethod('remote')}
                            style={{marginRight:6}}
                          />
                           
                        </label>
                        <label style={{display:'flex', alignItems:'center', cursor:'pointer'}}>
                          <input 
                            type="radio" 
                            name="paymentMethod" 
                            value="qr" 
                            checked={paymentMethod==='qr'} 
                            onChange={()=>setPaymentMethod('qr')}
                            style={{marginRight:6}}
                          />
                          QR-
                        </label>
                        <label style={{display:'flex', alignItems:'center', cursor:'pointer'}}>
                          <input 
                            type="radio" 
                            name="paymentMethod" 
                            value="transfer" 
                            checked={paymentMethod==='transfer'} 
                            onChange={()=>setPaymentMethod('transfer')}
                            style={{marginRight:6}}
                          />
                          
                        </label>
                      </div>
                      {paymentMethod === 'remote' && (
                        <div style={{color:'#666', fontSize:14, padding:'8px 12px', background:'#f5f5f5', borderRadius:4}}>
                                
                        </div>
                      )}
                      {paymentMethod === 'qr' && (
                        <div style={{color:'#666', fontSize:14, padding:'8px 12px', background:'#f5f5f5', borderRadius:4}}>
                          QR-       
                        </div>
                      )}
                      {paymentMethod === 'transfer' && (
                        <div style={{color:'#666', fontSize:14, padding:'8px 12px', background:'#f5f5f5', borderRadius:4}}>
                                 
                        </div>
                      )}
                    </div>
                  )}
                </div>

                {/*  */}
                <label style={{display:'flex', alignItems:'center', cursor:'pointer', marginTop:8}}>
                  <input 
                    type="radio" 
                    name="payment" 
                    value="cash" 
                    checked={payment==='cash'} 
                    onChange={()=>setPayment('cash')}
                    style={{marginRight:6}}
                  />
                  
                </label>
              </div>
            </div>

            {/*  */}
            <div className="checkout-form-container">
              <div style={{fontWeight:700, marginBottom:12}}>  </div>
              <div className="form-group">
                <textarea name="comment" value={form.comment} onChange={handleChange} placeholder=" " />
              </div>
            </div>
          </div>

          {/*   -  */}
          <div className="checkout-form">
            <div style={{ height: '60px', marginBottom: 0 }}></div> {/*       */}
            {product && (
              <div className="checkout-product-card">
                <img src={product.image || '/images/products/placeholder.png'} alt={product.name} style={{width: 72, height: 72, objectFit: 'contain', borderRadius: 8, background: '#fff', border: '1px solid #e0e0e0'}} />
                <div style={{flex:1}}>
                  <div style={{fontWeight:600}}>{product.name}</div>
                  <div style={{color:'#666', fontSize:14}}>1 .</div>
                </div>
                <div style={{fontWeight:700, color:'#1a2236'}}>{Number(total).toFixed(3).replace(/\.?0+$/, '')} </div>
              </div>
            )}

            <div className="checkout-form-container" style={{marginBottom:12}}>
              <div style={{display:'flex', justifyContent:'space-between', marginBottom:8}}>
                <span style={{color:'#666'}}> :</span>
                <span style={{fontWeight:600}}>{Number(total).toFixed(3).replace(/\.?0+$/, '')} </span>
              </div>
              {deliveryCost > 0 && (
                <div style={{display:'flex', justifyContent:'space-between', marginBottom:8}}>
                  <span style={{color:'#666'}}>:</span>
                  <span style={{fontWeight:600}}>{Number(deliveryCost).toFixed(3).replace(/\.?0+$/, '')} </span>
                </div>
              )}
              <div style={{display:'flex', justifyContent:'space-between', borderTop:'1px solid #e0e0e0', paddingTop:12}}>
                <span style={{fontWeight:600}}> :</span>
                <span style={{color:'#FFB300', fontWeight:700}}>{Number(finalTotal).toFixed(3).replace(/\.?0+$/, '')} </span>
              </div>
            </div>

            {siteSettings.showPromoCode && (
              <div className="checkout-form-container" style={{marginBottom:12}}>
                <div style={{display:'flex', gap:8}}>
                  <input type="text" placeholder=" " value={promo} onChange={(e)=>setPromo(e.target.value)} style={{flex:1}} />
                  <button type="button" className="submit-button" style={{padding:'12px 16px', width:160}}></button>
                </div>
              </div>
            )}

            <form onSubmit={handleSubmit}>
              <button type="submit" className="submit-button"> </button>
            </form>
          </div>
        </div>
      </main>
      <Footer />
    </div>
  );
};

export default Checkout; 